import React from 'react'
import { Card, CardImg, CardText, CardBody, CardTitle, CardSubtitle, Button } from 'reactstrap'
import { connect } from 'react-redux'
import './Appts.css'

const Appt = (props) => {

	const appt = props.appts.find(apt => apt.id === +props.match.params.id)
	let apptCard = <p>Appointment is loading...</p>
	if (appt) {
		apptCard = (
			<Card>
				<CardBody>
					<CardTitle>{appt.customer.firstname} {appt.customer.lastname}</CardTitle>
					<CardSubtitle>{appt.appt_date.split('T')[0].toString()}</CardSubtitle>
					<CardSubtitle>{appt.appt_start.split('T')[1].slice(0, 5).toString()}</CardSubtitle>
					<CardText>{appt.appt_note}</CardText>
					<Button>Download Appt</Button>
				</CardBody>
			</Card>
		)
	}

	return (
		<div>{apptCard}</div>
	)
}

const mapStateToProps = state => {
	return {
		appts: state.apt.appts
	}
}

export default connect(mapStateToProps)(Appt)



